/**
 * @description
 * Template used when the widget preference sets to multiple category selection
 *
 * @name checkboxTemplate
 * @type {string}
 */
const checkboxTemplate = `
  <div class="row">
    <div class="pt-0 pb-0 col-md-6 col-lg-6 col-sm-6" ng-repeat="item in $ctrl.options">
      <div class="checkbox m-0">
        <label class="d-flex align-items-center">
          <input type="checkbox" 
            name="categories" 
            value="{{item}}" 
            ng-model="$ctrl.ngModel[item]" />
          <span class="ml-2">{{item}}</span>
        </label>
      </div>
    </div>
  </div>
`;

/**
 * @description
 * Template used when the widget pref. is not setted to use multiple selections
 * and the amount of options are less or equal than 3
 *
 * @name radioTemplate
 * @type {string}
 */
const radioTemplate = `
  <div>
    <div class="radio" ng-repeat="item in $ctrl.options">
      <label>
        <input type="radio" name="categories" ng-value="item" ng-model="$ctrl.ngModel" />
        {{item}}
      </label>
    </div>
  </div>
`;

/**
 * @description
 * Template used when the widget pref. is not setted to use multiple selections
 * and the amount of options are more than 3
 *
 * @name dropdownSelectTemplate
 * @type {string}
 */
const dropdownSelectTemplate = `
  <ui-bb-dropdown-select
    data-ng-model="$ctrl.ngModel"
    data-ng-change="$ctrl.onChange({ '$item': $item })"
    data-selected-as="$option">
    <ui-bb-dropdown-option
      data-option="item"
      data-arrow-navigation="true"
      data-ng-repeat="item in $ctrl.options"
      class="list-group-item-text">
      {{$option}}
    </ui-bb-dropdown-option>
  </ui-bb-dropdown-select>
`;

/**
 * @name getTemplate
 * @type {function}
 *
 * @description
 * Function that defines what template is going to be used
 *
 * @param {string} multi widget preference that defines if it will be multiple or not
 * @param {Number} length length of the options
 * @returns {string} template to be used
 */
const getTemplate = (multi, length) => {
  if (multi) {
    return checkboxTemplate;
  }

  if (length <= 3) {
    return radioTemplate;
  }

  return dropdownSelectTemplate;
};

/**
 * @name uiBbAccountStatementCategoryFilterNg
 * @type {object}
 *
 * @property {object} ng-model Category model
 * @property {function} ng-change Callback function triggered when account is selected
 * @property {array} options List of options to show
 */
const component = {
  bindings: {
    ngModel: '=',
    onChange: '&',
    options: '<',
    multi: '<',
  },
  template: `
    <div ng-include src="'ui-bb-account-statements-category-filter-ng/template.html'"></div>
  `,
  controller: [
    '$templateCache',
    function controller($templateCache) {
      this.$onInit = () => {
        $templateCache.put('ui-bb-account-statements-category-filter-ng/template.html',
          getTemplate(this.multi, this.options.length));
      };
    },
  ],
};

export default component;



